.main-acceptbid {
    background-color: #ffffff;
    padding-bottom: 110px;
    button.btn-back {
        background: #ffffff;
        border: 1.5px solid #ffdbed;
        border-radius: 90px;
        text-align: center;
        align-items: center;
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        justify-content: space-between;
        align-items: unset;
        padding: 12px 16px;
        margin-top: 40px;
        margin-bottom: 45px;
        display: flex;
        &:hover {
            color: #ffffff;
            box-shadow: 0px 4px 4px #00000040;
            background-color: #ff0083;
        }
        img.img-fluid {
            padding-right: 14px;
        }
    }
    .maincard {
        background: #fafafa;
        border: 2px solid #ff0083;
        box-shadow: 0px 10px 50px #0000000d;
        border-radius: 10px;
        box-sizing: border-box;
        width: 522px;
        height: 534px;
        position: relative;
        .cardimgg {
            text-align: center;
            justify-content: center;
            align-items: center;
            // margin-top: 135px;
            position: absolute;
            left: 128.51px;
            top: 135.93px;
        }
        h3.cardinnerheading {
            font-family: "Goldman";
            font-style: normal;
            font-weight: 400;
            font-size: 50px;
            line-height: 120%;
            text-align: center;
            color: #ff0083;
            // padding-top: 237px;
            position: absolute;
            // right: 60px;
            left: 102px;
            top: 237px;
        }
    }
    .bid-card {
        padding: 24px;
        gap: 8px;
        width: 522px;
        height: 180px;
        background: #ffffff;
        border: 1px solid #ffdbed;
        border-radius: 8px;
        margin-top: 30px;
        .inner-content {
            h3 {
                font-weight: 700;
                font-size: 16px;
                line-height: 170%;
                color: #0f172a;
                margin-bottom: 8px;
            }
            p {
                font-style: normal;
                font-weight: 400;
                font-size: 16px;
                line-height: 170%;
                color: #fa2189;
                margin-bottom: 8px;
            }
            h5 {
                font-style: normal;
                font-weight: 400;
                font-size: 16px;
                line-height: 170%;

                color: #475569;
                margin-bottom: 8px;
            }
        }
    }
    .brdr {
        width: 100%;
        height: 0px;
        border: 1px solid #eceaea;
        margin-top: 15px;
    }
    .brdract {
        width: 100%;
        height: 0px;
        border: 1px solid #eceaea;
        margin-top: 15px;
        margin-bottom: 17px;
    }
    .heading-bid {
        h5 {
            font-style: normal;
            font-weight: 400;
            font-size: 35px;
            line-height: 100%;
            color: #ff0083;
            text-transform: lowercase;
            margin-bottom: 30px;
            font-family: "Goldman", serif;
        }
    }

    .information {
        margin-bottom: 94px;
        margin-top: 27px;
        .para {
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 130%;
            color: #777e90;
        }

        .avtars {
            margin-top: 29px;
            display: flex;
            .avtarfirst {
                display: flex;
                ul.autmint {
                    margin-left: 10px;
                    .Minted {
                        font-style: normal;
                        font-weight: 500;
                        font-size: 12px;
                        line-height: 100%;
                        color: #777e90;
                        margin-bottom: 5px;
                    }
                    .Tim {
                        font-style: normal;
                        font-weight: 600;
                        font-size: 16px;
                        line-height: 100%;
                        color: #131524;
                    }
                }
            }
        }
    }

    .bidActivity {
        margin-top: 29px;
        .Activity-heads {
            margin-left: 15px;
        }
        .maaiinnoonneeact {
            display: flex;
            align-items: center;
        }

        .transferred {
            font-style: normal;
            font-weight: 600;
            font-size: 13px;
            line-height: 20px;
            color: #1d1d1d;
            margin-bottom: 3px;
            span {
                font-style: normal;
                font-weight: 500;
                font-size: 13px;
                line-height: 20px;
                color: #ff0083;
                margin-left: 2px;
            }
        }
        .secransferred {
            font-style: normal;
            font-weight: 500;
            font-size: 13px;
            line-height: 20px;
            color: #777e90;
            span.fst {
                font-style: normal;
                font-weight: 400;
                font-size: 13px;
                line-height: 20px;
                color: #ff0083;
                margin-left: 2px;
            }
            span.sec {
                font-style: normal;
                font-weight: 400;
                font-size: 13px;
                line-height: 20px;
                color: #777e90;
                margin-left: 10px;
            }
        }
    }

    .bidhistory {
        // margin-top: 29px;
        overflow-y: scroll;
        .content-history {
            display: flex;
            justify-content: space-between;
        }
        ul.list-inline.maaiinnoonnee {
            display: flex;
            // justify-content: center;
            // text-align: start;
        }

        h3 {
            font-style: normal;
            font-weight: 600;
            font-size: 13px;
            line-height: 20px;
            color: #1d1d1d;
            span {
                font-style: normal;
                font-weight: 400;
                font-size: 13px;
                line-height: 20px;
                color: #ff0083;
                padding-left: 8px;
            }
        }
        .hours {
            font-style: normal;
            font-weight: 400;
            font-size: 13px;
            line-height: 20px;
            color: #777e90;
        }
        img.img-fluid.sfgsdfsdsdgsdg {
            padding-right: 16px;
        }
        li.list-inline-item.shoqatali {
            text-align: center;

            h4.shoqii {
                font-style: normal;
                font-weight: 600;
                font-size: 13px;
                line-height: 20px;
                /* text-align: right; */
                color: #1d1d1d;
            }
            p.sectime {
                font-style: normal;
                font-weight: 400;
                font-size: 13px;
                line-height: 20px;
                /* text-align: right; */
                color: #777e90;
            }
        }
    }
    .lastcardbtn {
        margin: auto;
        position: fixed;
        margin-top: 41px;

        .main-cardsbid {
            display: flex;

            align-items: center;
            .history-cards {
                width: 100%;
                height: auto;
                // margin-top: 88px;
                background: #fafafa;
                border: 1px solid #ffdbed;
                border-radius: 10px;
                padding: 20px;
                margin-right: 10px;
                margin-bottom: 20px;
                &:last-child {
                    margin-right: 0 !important;
                }
                .history-content {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    h5 {
                        font-style: normal;
                        font-weight: 500;
                        font-size: 14px;
                        line-height: 100%;
                        /* identical to box height, or 14px */

                        color: #777e90;
                    }
                    .history-inner {
                        display: flex;
                        align-items: center;
                        p {
                            font-weight: 600;
                            font-size: 16px;
                            line-height: 100%;
                            text-transform: uppercase;
                            color: #1d1d1d;
                        }
                    }
                }
            }
        }

        .btnbtnbid {
            align-items: center;
            padding: 16px 40px;
            gap: 10px;
            width: 100%;
            height: 48px;
            background: #ff0083;
            box-shadow: 0px 18px 30px -10px #ff008326;
            border-radius: 5px;
            font-weight: 600;
            font-size: 16px;
            line-height: 100%;
            border: none;
            color: #ffffff;
            border: 1px solid #ff0083;
            outline: none;
            &:hover {
                color: #ff0083;
                box-shadow: 0px 4px 4px #00000040;
                background-color: #ffffff;
            }
        }
    }
    .nav-tabs .nav-link {
        font-style: normal;
        font-weight: 500;
        font-size: 15px;
        line-height: 26px;
        border: none;

        color: #777e90;
        text-align: start;
        justify-content: flex-start;
        align-items: center;
        align-content: start;
    }
    .nav-tabs .nav-link.active {
        color: #ff0083;
        border: none;
        font-style: normal;
        font-weight: 600;
        font-size: 15px;
        line-height: 26px;
        border-bottom: 3px solid #ff0083;
    }
    .modal-content {
        width: 100%;
        padding: 0px 0px 30px;
        background: #ffffff;
        border-radius: 10px;
        .modal-header {
            background: #fafafa;
            padding-bottom: 30px;
            h3 {
                font-style: normal;
                font-weight: 600;
                font-size: 22px;
                line-height: 100%;
                /* identical to box height, or 22px */
                text-align: center !important;
                text-transform: uppercase;
                width: 100%;
                color: #121212;
            }
            .close {
                span {
                    position: absolute;
                    top: 10px;
                    right: 30px;
                }
            }
        }
        .modal-body {
            padding-top: 30px;
            padding-left: 30px;
            padding-right: 30px;
            .btnmodalbtn {
                padding: 16px 40px;
                gap: 10px;
                margin-top: 26px;
                width: 100%;
                border: none;
                background: #ff0083;
                box-shadow: 0px 18px 30px -10px rgba(255, 0, 131, 0.15);
                border-radius: 5px;
                font-weight: 600;
                font-size: 16px;
                line-height: 100%;
                /* identical to box height, or 16px */
                color: #ffffff;
            }
            p {
                font-style: normal;
                font-weight: 500;
                font-size: 16px;
                line-height: 130%;
                color: #696969;
                margin-bottom: 36px;
            }
            span {
                color: #ff0083;
            }
            .text-modalbid {
                display: flex;
                justify-content: space-between;
                align-items: center;
                .head {
                    font-style: normal;
                    font-weight: 600;
                    font-size: 24px;
                    line-height: 130%;
                    /* or 31px */
                }
                .head1 {
                    font-style: normal;
                    font-weight: 500;
                    font-size: 16px !important;
                    line-height: 120%;
                    /* or 19px */
                    text-align: right;
                    /* blueGray/900 */
                    color: #0f172a;
                }
                .bidhead {
                    font-style: normal;
                    font-weight: 500;
                    font-size: 16px;
                    line-height: 100%;
                    /* identical to box height, or 16px */
                    color: #121212;
                }
                .innertext-modalbid {
                    display: flex;
                    align-items: baseline;
                }
            }
        }
    }
}

